export default {
    onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
        this.id = option.id//打印出上个页面传递的参数。
		console.log(option.id)
    },
	data(){
		return {
			id:'',
			WorkDailyDetailDto:'',
			photos:'',
			Descriptions:'',
			Plans: '',//计划
			
		}
	},
	mounted(){
		uni.request({
			url: this.$store.state.url + '/PCenter/GetWorlDaily',
			method: 'GET',
			data: {
				workdailyId:this.id 
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				var asd = JSON.parse(res.data).Data
				this.WorkDailyDetailDto = asd.WorkDailyDetailDto
				this.Plans = asd.Plans
				this.photos = asd.Photos
				this.Descriptions = asd.Descriptions
				console.log(asd)
			}
		});
	}
}

